import React from "react"
import PropTypes from "prop-types"
import { ThemeProvider } from "styled-components"
import { GlobalStyle } from "../../style/globalStyle"
import { useLineHelper } from "../../style/useLineHelper"
import { useThemeSelector, setInitialTheme } from "../../style/useThemeSelector"
import { Header } from "../Header"
import * as themes from '../../style/themes';

export const ToggleThemeProvider = React.createContext({
  toggleTheme: () => null,
})

// useLineHelper()
export const BlogLayout = ({ children }) => {
  // const [theme, toggleTheme] = useThemeSelector()
  // setInitialTheme();
  let theme = themes.lightTheme;
  if (process.browser) {
    theme = themes[localStorage.getItem("theme")];
  }
  return (
    <ThemeProvider theme={theme}>
      <ToggleThemeProvider.Provider value={{ toggleTheme: () => null, }}>
        <Header />
        <div className="children main_width">{children}</div>
        <GlobalStyle />
      </ToggleThemeProvider.Provider>
    </ThemeProvider>
  )
}

BlogLayout.propTypes = {
  children: PropTypes.node.isRequired,
}
